var timeId;
export default {
	data() {
		return {
			imgUrl: '',
			time: 5,
		};
	},
	onLoad() {
		this.getConfig()
	},
	onUnload() {
		clearInterval(timeId)
	},
	methods: {
		goPage() {
			uni.reLaunch({
				url: '/pages/index/index'
			})
		},
		getConfig() {
			this.$app.request('open/today', {}, res => {
				if (!res.data) {
					this.goPage()
				} else {
					// 开屏图
					this.imgUrl = res.data
					timeId = setInterval(() => {
						if (--this.time <= 0) {
							clearInterval(timeId)
							this.goPage()
						}
					}, 1000)
				}

			})
		},
	},
}
